import { WebUrl } from "../../../constants";

const eventOddVal = (w) => w % 2 == 0 ? 'even' : 'odd';
export const startUpUrl = (networkIdList, wallId) => {
    const isEven = eventOddVal(wallId);
    const anchorTextTemplates = {
        default: [{ text: 'Best Widget for Website', isAnchor: true }],
        embedUgcWebsite: [
            { text: 'Embed', isAnchor: false },
            { text: 'User Generated Content', isAnchor: true },
            { text: 'on Website', isAnchor: false }
        ],
        embedUgcWidget: [
            { text: 'Embed', isAnchor: false },
            { text: 'UGC', isAnchor: true },
            { text: 'Widget', isAnchor: false }
        ],
        customTexts: {
            4: [{ text: 'Embed Google feed on your website', isAnchor: true }],
            12: [{ text: 'Embed Rss feed on website', isAnchor: true }],
            18: [{ text: 'Embed Instagram feed on your website', isAnchor: true }],
            19: [{ text: 'Embed Yelp feed on website', isAnchor: true }],
            23: [{ text: 'Embed Airbnb reviews on website', isAnchor: true }],
            28: [{ text: 'Embed Tiktok videos on website', isAnchor: true }]
        }
    };

    const baseLinks = {
        default: `${WebUrl}widget/`,
        blog: `${WebUrl}blog/`,
        googleFeed: `${WebUrl}blog/embed-google-reviews-on-website/`,
        rssFeed: `${WebUrl}blog/add-rss-feed-on-website/`,
        instagramFeed: `${WebUrl}blog/embed-instagram-feed-on-website/`,
        yelpFeed: `${WebUrl}blog/embed-yelp-reviews-on-website/`,
        airbnbFeed: `${WebUrl}blog/embed-airbnb-reviews-on-website/`,
        tiktokFeed: `${WebUrl}blog/embed-tiktok-video-on-website/`
    };

    // Filter and prioritize networkIdList
    if (Array.isArray(networkIdList) && networkIdList.length > 1) {
        const filteredList = networkIdList.filter(id => [1, 3, 7, 10].includes(id));
        if (filteredList.length > 1) {
            if (filteredList.includes(3)) {
                networkIdList = [3];
            } else {
                return [baseLinks.blog, anchorTextTemplates.embedUgcWebsite];
            }
        }
    }

    const primaryNetworkId = Array.isArray(networkIdList) && networkIdList.length === 1 ? networkIdList[0] : 0;

    switch (primaryNetworkId) {
        case 1:
        case 3:
        case 7:
            return [
                baseLinks.blog,
                isEven === 'even'
                    ? anchorTextTemplates.embedUgcWebsite
                    : [
                        { text: 'Embed', isAnchor: false },
                        { text: 'UGC', isAnchor: true },
                        primaryNetworkId === 3
                            ? { text: 'Widget on Website', isAnchor: false }
                            : { text: 'Widget', isAnchor: false }
                    ]
            ];
        case 4:
            return [baseLinks.googleFeed, anchorTextTemplates.customTexts[4]];
        case 10:
            return [
                baseLinks.blog,
                [
                    { text: 'Embed', isAnchor: false },
                    { text: 'User Generated Content on Website', isAnchor: true }
                ]
            ];
        case 12:
            return [baseLinks.rssFeed, anchorTextTemplates.customTexts[12]];
        case 18:
            return [baseLinks.instagramFeed, anchorTextTemplates.customTexts[18]];
        case 19:
            return [baseLinks.yelpFeed, anchorTextTemplates.customTexts[19]];
        case 23:
            return [baseLinks.airbnbFeed, anchorTextTemplates.customTexts[23]];
        case 28:
            return [baseLinks.tiktokFeed, anchorTextTemplates.customTexts[28]];
        default:
            return [baseLinks.default, anchorTextTemplates.default];
    }
};
const CustomBranding = ({ appData }) => {
    let networks = appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null;
    const startupDtls = startUpUrl(networks, appData.wallID);
    return <div
        ref={(el) => {
            if (el) {
                el.style.setProperty('align-items', 'center', 'important');
                el.style.setProperty('display', 'flex', 'important');
                el.style.setProperty('width', '100%', 'important');
                el.style.setProperty('text-decoration', 'none', 'important');
                el.style.setProperty('justify-content', 'end', 'important');
                el.style.setProperty('color', '#0b57d0', 'important');
            }
        }}
    >{
            startupDtls[1].map((item, index) => item.isAnchor ? <a key={index} href={startupDtls[0]} target="_blank" aria-label={item.text}
                ref={(el) => {
                    if (el) {
                        el.style.setProperty('background-color', 'rgba(250, 250, 250, 1)', 'important');
                        el.style.setProperty('align-items', 'center', 'important');
                        el.style.setProperty('display', 'flex', 'important');
                        el.style.setProperty('width', 'auto', 'important');
                        el.style.setProperty('text-decoration', 'none', 'important');
                        el.style.setProperty('padding', '10px 14px', 'important');
                        el.style.setProperty('color', '#0b57d0', 'important');
                        el.style.setProperty('font-size', '13px', 'important');
                    }
                }}>
                {item.text}
            </a> : item.text)
        }
    </div>
}

export default CustomBranding;